import React from 'react';
import PropTypes from 'prop-types';

import { 
	Panel, 
	PanelHeader, 
	PanelHeaderBack, 
	Banner,
	Header,
	Group,
	Div,
} from '@vkontakte/vkui';

import { 
	Icon24List,
	Icon24ClockOutline,
} from '@vkontakte/icons';

const Schedule = ({ id, go }) => (
	<Panel id={id}>
		<PanelHeader left={<PanelHeaderBack onClick={go} data-to="more" />}>Расписание</PanelHeader>
		
		<Group>
			<Banner 
				header="Расписание уроков"
				subheader=""
				asideMode="expand"
				before={<Icon24List fill="#3883A8" />}
				onClick={go} data-to="schedule-lesson"
			/>

			<Banner 
				header="Расписание звонков"
				subheader=""
				asideMode="expand"
				before={<Icon24ClockOutline fill="#3883A8" />}
				onClick={go} data-to="schedule-times"
			/>

		</Group>
	</Panel>
);

Schedule.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Schedule;