import React from 'react';
import PropTypes from 'prop-types';
import bridge from '@vkontakte/vk-bridge';

import { 
	Panel, 
	PanelHeader, 
	PanelHeaderBack, 
	Banner,
	Tabbar,
	TabbarItem,
	Group,
	Footer,
} from '@vkontakte/vkui';

import { 
	Icon24Users,
	Icon24Share,
	Icon28QuestionOutline,
	Icon24Newsfeed,
} from '@vkontakte/icons';

const More = ({ id, go }) => (
	<Panel id={id}>
		<PanelHeader>Ещё</PanelHeader>
		
		<Group>
			<Banner
				header="Состав"
				subheader=""
				asideMode="expand"
				before={<Icon24Users fill="#3883A8" />}
				onClick={go} data-to="composition"
			/>

			<Banner
				header="Поделиться с друзьями"
				subheader=""
				asideMode="expand"
				before={<Icon24Share fill="#3883A8" />}
				onClick={() => bridge.send("VKWebAppShare", {"link": "https://vk.com/app7820245"})} 
			/>

			<Banner
				header="FAQ - Ответы на более популярные вопросы"
				subheader=""
				asideMode="expand"
				before={<Icon28QuestionOutline width={24} height={24} fill="#3883A8" />}
				onClick={go} data-to="faq"
			/>

			<Banner 
				header="Расписание"
				subheader=""
				asideMode="expand"
				before={<Icon24Newsfeed fill="#3883A8" />}
				onClick={go} data-to="schedule"
			/>

		</Group>

		<Footer>Версия программы: 1.0.6</Footer>
	</Panel>
);

More.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default More;
